// SB Field Variables:
// --sb-field-width - width of field
// --sb-field-label-color - color of label text
// --sb-field-required-color - color of required label text
// --sb-field-feedback-color - color of feedback label text

.sb-field {
  width: var(--sb-field-width, unset);
}

.sb-field__label {
  display: flex;
  font-size: var(--sb-field-label-size, var(--sbt-font-size-25));
  font-weight: var(--sbt-font-weight-40);
  line-height: var(--sbt-line-height-ui);
  margin-bottom: var(--sb-field-label-margin, 4px);

  .sb-field__label-text {
    color: var(--sb-field-label-color, var(--sbt-theme-tertiary-color-90));
  }

  .sb-field__label-required-text {
    color: var(--sb-field-required-color, var(--sbt-theme-tertiary-color-50));
    margin-left: auto;
    padding-left: var(--sbt-spacing-20);
  }
}

.sb-field__feedback {
  color: var(--sb-field-feedback-color, var(--sbt-theme-tertiary-color-50));
  display: block;
  font-size: var(--sb-field-feedback-size, var(--sbt-font-size-20));
  line-height: var(--sbt-line-height-50);

  &--has-error {
    color: var(--sbt-color-error-50);
    font-weight: var(--sbt-font-weight-60);
  }

  &--has-success {
    color: var(--sbt-color-success-50);
    font-weight: var(--sbt-font-weight-60);
  }
}
