/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-cp-tooltip-template {
  --sb-tooltip-font-size: var(--sbt-font-size-20);
  --sb-tooltip-font-weight: var(--sbt-font-weight-60);
  background-color: var(
    --sb-tooltip-template-background-color,
    var(--sbt-theme-tertiary-color-60)
  );
  color: var(--sb-tooltip-template-color, var(--sbt-theme-tertiary-color-20));

  .tippy-arrow::before {
    color: var(
      --sb-tooltip-template-background-color,
      var(--sbt-theme-tertiary-color-60)
    );
  }
}

.sb-cp-tooltip-template__icon {
  --sb-icon__color: ar(
    --sb-tooltip-template-icon-color,
    var(--sbt-theme-tertiary-color-60)
  );
  margin-left: var(--sbt-spacing-20);
  padding: 0;
}
